.averd-menu {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
.averd-menu .averd-menu-item {
  padding: 0 24px;
  white-space: nowrap;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.averd-menu .averd-menu-item:hover {
  color: #722ed1;
}
.averd-menu .averd-menu-item.averd-menu-item-active {
  background-color: rgba(114, 46, 209, 0.05);
  border-color: #722ed1;
}
.averd-menu-horizontal {
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
      -ms-flex-direction: row;
          flex-direction: row;
  border-bottom: 1px solid #d9d9d9;
}
.averd-menu-horizontal .averd-menu-item {
  line-height: 46px;
  border-bottom: 2px solid transparent;
}
.averd-menu-vertical {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
      -ms-flex-direction: column;
          flex-direction: column;
  border-right: 1px solid #d9d9d9;
}
.averd-menu-vertical .averd-menu-item {
  margin: 3px 0;
  line-height: 36px;
  border-right: 3px solid transparent;
}
