.averd-tag {
  display: inline-block;
  margin-right: 8px;
  margin-bottom: 8px;
  padding: 3px 6px;
  font-size: 0.85rem;
  background: #fafafa;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
}
.averd-tag-magenta {
  color: #eb2f96;
  background-color: rgba(235, 47, 150, 0.2);
  border-color: rgba(235, 47, 150, 0.6);
}
